<template>
<div>
	<div class="chooser-component">
		<ul class="chooser-list">
			<li 
				v-for="(item, index) in selections" 
				v-text="item.label"
				@click="choseSelection(index)"
				:class="{active: nowIndex === index}"
				></li>
		</ul>
	</div>
</div>
</template>

<script>
	export default {
		name: 'chooser',
		props: {
			selections: {
				type: Array,
				default () {
					return [{
						label: 'demo',
						value: 0
					}]
				}
			}
		},
		data () {
			return {
				nowIndex: 0
			}
		},
		computed: {
			
		},
		methods: {
			choseSelection (index) {
				this.nowIndex = index;
				this.$emit('color-select', index)
			}
		}
	}
</script>

<style scoped>
	.chooser-component {
	  position: relative;
	  display: inline-block;
	}
	.chooser-list li{
	  display: inline-block;
	  border: 1px solid #e3e3e3;
	  height: 25px;
	  line-height: 25px;
	  padding: 0 8px;
	  margin-right: 5px;
	  border-radius: 3px;
	  text-align: center;
	  cursor: pointer;
	}
	.chooser-list li.active {
	  border-color: #4fc08d;
	  background: #4fc08d;
	  color: #fff;
	}
</style>


